import globalVars from "../../../library/globalVariables";

const WebFormOptions = ({ optionSelect, addHiddenSelect}) => {
  const options = optionSelect[0].fieldOptions.map(option => (
    <option key={option.id} value={option.value}>
      {option.value}
    </option>
  ));

  return (
    <select onChange={addHiddenSelect}>
      <option key={globalVars.blankOption}>{globalVars.blankOption}</option>
      {options}
    </select>
  );
};

export default WebFormOptions;
